import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';


const VLinkColor = "#54412e"
const LinkColor = "#593108"
const ALinkColor = "#ba844e"

const ActionStyles = styled.section`
width: 100%;
min-height: 100vh;

header{
   width: 854px;
   height:480px;
   margin: 0 auto;
   padding-top: 1rem;
   iframe{
    box-shadow: 4px 4px 5px 0px #2e251e80;
   }  
}

main{
    width: 854px;
    margin: 1rem auto;
}

footer{
    width: 854px;
    margin: auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    h3 {
        font-size: 1.1rem;
    }
}

.CallBack{
    flex: 1 0 100%;
    text-align: center;
}

a.External{
    flex: 1 0 40%;
    margin:20px;
    display: flex;
}


`

export const CallToAction = () =>
    <ActionStyles>
        <header>
        <iframe frameborder="0" scrolling="no" marginheight="0" marginwidth="0"width="854" height="480" src="https://www.youtube.com/embed/h7SuutuGQC8"></iframe>
        </header>
        <main>
        <h2>Aleesa Pixel Tana</h2>
        <p>Iâ€™m a pencil-and-paper animator that loves pixel art, low-poly 3D models, that also knows how to write UI and shader code, while contributing meaningfully to discussions of game design and user experience.  I enjoy creating character artwork that evokes a living story and utilizes a variety of cartoony styles.  I am greatly inspired by manga, comic book and animation artists including Bryan Konietzko, Wendy Pini, CLAMP, Gurihiru, Bruce Timm, Naoko Takeuchi and many more.I love working in colerase pencils, alcohol markers, gouache, pixel art and hand painted 3D.  I enjoy taking my art beyond still flat pictures by introducing movement, either through animation, interactivity, three-dimensionality and the play of light. I want to make art that inspires others to be creative, and encourages others that, like me, have suffered trauma, to believe that healing is possible through creativity.
        </p>
        
        <h2>LIZ TANA</h2>
        <p>I have a hard time making connections and a weird skill set for making games, so I'm starting my published games career later than some. I grew up in some rough circumstances and am mostly self-educated and fringes of online community educated. I deal with a lot of anxiety, social and otherwise, so I am turning to this medium to help me get past that. I'm part of a weird walk of life, and that doesn't always get me much positive attention. I have begun making games that evoke calm, have an focus on relating game concepts to real life. I'm focused on meditative games focused toward puzzles, exploration of space, concepts, and character. I'm working to improve my skills at creating satisfyingly weighty character movement mechanics, as well as music and soundscape production &amp; editing.     
        </p>
        </main>
        <footer>
        <Link className="CallBack" to="/about"><h1>Visit at our commision gallery to get a good look at what we do</h1></Link>
        <a className="External" href="https://www.patreon.com/Aleesatana"><img src="/assets/Patreon_Mark_White.png" alt="Patreon" width="80px" height="80px"/>
        <h3>To support our ongoing publishing efforts, come see our Patreon</h3></a>
        <a className="External" href="https://discord.gg/kSQp9p3"><img src="/assets/Discord-Logo-White.png" alt="Discord" width="80px" height="80px"/>
        <h3>Join us on Discord to discuss your commission</h3></a>
        </footer>
    </ActionStyles>