import React, {useState} from 'react';
import styled from 'styled-components';

export const GalleryItem = styled.section`
position: relative;
background: #68894D50;
flex: 2 0 44%;
max-width: 300px;
display: flex;
flex-flow: column nowrap;
box-shadow: 4px 4px 5px 0px #2e251e80;


.ImageBox {
    width: fit-content;
    margin: 0 auto;
}

h2 {
    font-size: 1.33rem;
    margin: 0.33rem 1.3rem;
}

.Description{ 
    margin: 0 1.3rem;
}

h3 {
    width: fit-content;
    position: absolute;
    right: 1rem;
    top: 1.66rem;
    font-size: 1.33rem;
    background: white;
    transform: rotate(30deg);
    box-shadow: 5px 5px;
}
.Modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    min-height: 100vh;
    background: #000000B0

        img, iframe{
            padding-top: 2rem;
            display: block;
            margin: auto;
        }
    }
.Modal-enter{
        opacity: 0;
        z-index: 10;
    }
.Modal-enter-active{
        opacity: 1;
        transition: all 666ms;
        z-index: 10;
    }
.Modal-exit{
        opacity: 1;
        z-index: 10;
    }
.Modal-exit-active{
        opacity: 0;
        z-index: -1;
        transition: all 666ms;
    }
`

export const GalleryImg = (props) => {
    var [hide,setHide] = useState(true);
    const handleHideClick = (event) => {
        setHide(!hide);
        console.log("Hide is now ", hide);
    }
    var combineProps = {...props,hide,toggleHide:handleHideClick}
    return <InnerGalleryImg {...combineProps} />
}

const InnerGalleryImg = ({srcHint,alt,price,children,hide,toggleHide}) =>
    <GalleryItem key={srcHint}>
        <h2>{alt}</h2>
        <div onClick={toggleHide} 
        className='ImageBox'>
            <img src={`/assets/${srcHint}Thumb.png`} alt={alt} />
            <h3>${price}</h3>
        </div>
        <div className="Description">
            {children}
        </div>
        <div onClick={toggleHide} 
        className={`Modal 
        ${hide? "Modal-exit Modal-exit-active":"Modal-enter Modal-enter-active" }`}>
            <img src={`/assets/${srcHint}Large.png`} alt={alt} />
        </div>
        
    </GalleryItem>