import React from 'react';
import styled from 'styled-components';

const GalleryStyles = styled.section`
width: 100%;
min-height: 100vh;
margin: 0;

.GalleryImages {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
}
.PageTitle{
    margin: .6rem;
    margin-left:7rem;
    font-size: 4rem;
}
`

export const GalleryPage = ({title,background,children}) =>
    <GalleryStyles background={background}>
        <h1 className="PageTitle">{title}</h1>
        <section className="GalleryImages">
            {children}
        </section>
    </GalleryStyles>