import React, {Fragment} from 'react';
import styled from 'styled-components';
import {
  Link,
  useLocation,
  useParams
} from "react-router-dom";

const ImageCarosel = ({photos}) => {
    let output = []
    for (let index = 0; photos.length > index; index++){
        output.push(<img key={index} src={photos[index].url} alt={`${photos[index].name}`}/>)
    }
    return <Fragment>{output}</Fragment>
}

export const BannerBlock = ({name="", photos=[], children, background}) => <BaseBlock background={background}>
<main>
<div className="Description">
<Link to={`/${name.toLowerCase()}`}><h1 className="BannerHeading">{name}</h1></Link>
{children}
</div>
<div className="Images">
<Link to={`/${name.toLowerCase()}`}>
<ImageCarosel photos={photos} />
</Link>
</div>
</main>
</BaseBlock>

export const BaseBlock = styled.section`
box-sizing: border-box;
display: flex;
flex-direction: column;
justify-content: center;

p {
    font-weight: bold;
    font-size: 1.133rem
    line-height: 0.9
}

main {
    display: flex;
    margin: 2rem;
    margin-left:8rem;
    .BannerHeading{
        margin: 0;
        font-size: 3rem;
        color: #68894D;
    }
    .Description{
        padding-left: 2.33rem;
        order: 2
    }
    .Images{
        order: 1
    }    
}


`
export default BaseBlock