import React, {Fragment} from "react";
import {
  TransitionGroup,
  CSSTransition
} from "react-transition-group";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect,
  useLocation,
  useParams
} from "react-router-dom";

import {IllustrationCallout, IllustrationGallery} from './Illustration'
import {ModelsCallout,ModelsGallery} from './Models';
import {Transitions} from './Transitions';
import { CallToAction } from "./CallToAction";
import {CalloutButton} from './CallOutButton';
import { PixelGallery, PixelCallout } from "./PixelArt";

export default function AnimationRouteWrapper() {
  return (
    <Transitions>
      <Router>
        <CalloutButton/>
        <Switch>
          <Route exact path="/">
            <Redirect to="/about" />
          </Route>
          <Route path="*">
            <AnimatedTransitions />
          </Route>
        </Switch>
      </Router>
    </Transitions>
  );
}


function AnimatedTransitions() {
  let location = useLocation();
  return (
    <TransitionGroup>
    <CSSTransition
      key={location.key}
      classNames="zoom-in"
      timeout={900}
    >
      <Switch location={location}>
        <Route exact path="/">
            <Redirect to="/about" />
          </Route>
          <Route exact path="/about">
            <div>
              <IllustrationCallout />
              <ModelsCallout />
              <PixelCallout />
            </div>
          </Route>
          <Route exact path="/illustrations">
            <IllustrationGallery />
          </Route>
          <Route exact path="/callToAction">
            <CallToAction />
          </Route>
          <Route exact path="/models">
            <ModelsGallery />
          </Route>
          <Route exact path="/pixelart">
            <PixelGallery/>
          </Route>
      </Switch>
    </CSSTransition>
  </TransitionGroup>
  );
}